.homePage-ipad {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #13142c;
}
.homePage-ipad p {
  margin: 0;
  padding: 0;
}
.homePage-ipad header {
  position: relative;
  width: 100vw;
  height: 44vw;
  background: url(/static/media/bannerBg@2x.87d547e4.png) no-repeat;
  background-size: 100%;
  margin-bottom: 100px;
}
.homePage-ipad header hgroup {
  position: absolute;
  width: 100%;
  top: 34vw;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  color: #fff;
  text-align: center;
  z-index: 3;
}
.homePage-ipad header hgroup .banner-p1 {
  font-size: 3.28rem;
  font-family: PingFang-SC-Heavy, PingFang-SC;
  font-weight: 800;
  color: #ffffff;
  line-height: 4.57rem;
  letter-spacing: 1px;
  text-shadow: 0px 0px 1px rgba(100, 223, 222, 0.6);
  -webkit-animation: bannerP1 5s;
          animation: bannerP1 5s;
}
.homePage-ipad header hgroup .banner-p2 {
  font-size: 1.85rem;
  font-family: PingFang-SC-Bold, PingFang-SC;
  font-weight: bold;
  color: #64dfde;
  line-height: 2.71rem;
  text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.3);
  -webkit-animation: bannerP1 5s;
          animation: bannerP1 5s;
}
.homePage-ipad main {
  background-color: #13142c;
}
.homePage-ipad main .explanation-box {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.homePage-ipad main li {
  width: 21vw;
}
.homePage-ipad main li .explanation-head {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 1.57rem;
}
.homePage-ipad main li .explanation-head .explanation-head-img {
  width: 6.6vw;
  height: 8.62vw;
  line-height: 8.62vw;
}
.homePage-ipad main li .explanation-head .explanation-head-img img {
  width: 100%;
}
.homePage-ipad main li .explanation-head .explanation-head-title {
  margin-top: 20px;
  width: 11.9vw;
  min-height: 7vw;
  text-align: center;
  font-size: 1.57rem;
  font-family: PingFang-SC-Heavy, PingFang-SC;
  font-weight: 800;
  color: #64dfde;
  line-height: 2.14rem;
  letter-spacing: 1px;
  text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.6);
  background-image: url(/static/media/feature-detail-bg@2x.80636bbb.png);
  background-size: 10vw;
  background-repeat: no-repeat;
  background-position: center;
}
.homePage-ipad main li p {
  font-size: 1rem;
  font-family: PingFangSC-Light, PingFang SC;
  font-weight: 300;
  color: #ffffff;
  line-height: 1.4rem;
}
.homePage-ipad main .contact-us-box {
  position: relative;
  margin-top: 16vw;
  margin-bottom: 4vw;
}
.homePage-ipad main .contact-us-box .contact-us-bg {
  position: relative;
  height: 44vw;
}
.homePage-ipad main .contact-us-box .contact-us-bg img {
  position: absolute;
}
.homePage-ipad main .contact-us-box .contact-us-bg img:nth-child(1) {
  width: 84vw;
  left: 12.1vw;
}
.homePage-ipad main .contact-us-box .contact-us-bg img:nth-child(2) {
  width: 13.2vw;
  right: 26.5%;
  top: -21%;
  -webkit-animation: contactLogo-ipad 4s linear infinite alternate;
          animation: contactLogo-ipad 4s linear infinite alternate;
}
.homePage-ipad main .contact-us-box .contact-us-bg img:nth-child(3) {
  width: 13vw;
  right: 26.5%;
  top: -3%;
  -webkit-animation: contactAdorn 5s linear infinite;
          animation: contactAdorn 5s linear infinite;
}
.homePage-ipad main .contact-us-box .contact-us-bg img:nth-child(4) {
  width: 10vw;
  left: 17.5%;
  top: 35%;
  opacity: .6;
  -webkit-animation: contactAdorn2 5s linear infinite;
          animation: contactAdorn2 5s linear infinite;
}
.homePage-ipad main .contact-us-box .contact-us-bg img:nth-child(5) {
  width: 10vw;
  left: 17.5%;
  top: 14%;
  opacity: .6;
  -webkit-animation: contactAdorn3 5s linear infinite;
          animation: contactAdorn3 5s linear infinite;
}
.homePage-ipad main .contact-us-box .contact-us-txt {
  position: absolute;
  left: 8.5%;
  top: -3vw;
}
.homePage-ipad main .contact-us-box .contact-us-txt p {
  margin-left: 20px;
  font-size: 24px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #ffffff;
  line-height: 33px;
  text-shadow: 0px 2px 14px rgba(0, 214, 255, 0.5);
  margin-bottom: 1rem;
}
.homePage-ipad main .contact-us-box .contact-us-txt .uploadBtn {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 28.9vw;
  height: 4.58rem;
  margin-bottom: 1vw;
  cursor: pointer;
}
.homePage-ipad main .contact-us-box .contact-us-txt .uploadBtn .btn-bg {
  position: absolute;
  width: 100%;
}
.homePage-ipad main .contact-us-box .contact-us-txt .uploadBtn span {
  font-size: 24px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #ffffff;
  line-height: 2.42rem;
  text-shadow: 0px 2px 14px rgba(0, 214, 255, 0.5);
}
.homePage-ipad main .contact-us-box .contact-us-txt .uploadBtn .btn-icon {
  width: 4.5vw;
  margin-left: 25px;
  margin-right: 10px;
}
.homePage-ipad main .contact-us-box .contact-us-txt .uploadBtn .googlePlay {
  margin-right: 7px;
}
@-webkit-keyframes contactLogo-ipad {
  0% {
    top: -21%;
    opacity: 0;
  }
  50% {
    top: -24%;
    opacity: 1;
  }
  100% {
    top: -27%;
    opacity: 0;
  }
}
@keyframes contactLogo-ipad {
  0% {
    top: -21%;
    opacity: 0;
  }
  50% {
    top: -24%;
    opacity: 1;
  }
  100% {
    top: -27%;
    opacity: 0;
  }
}
