footer {
  background: -webkit-gradient(linear, right top, left top, from(rgba(100, 223, 222, 0.04)), color-stop(47%, rgba(100, 223, 222, 0.1)), to(rgba(100, 223, 222, 0.04)));
  background: linear-gradient(270deg, rgba(100, 223, 222, 0.04) 0%, rgba(100, 223, 222, 0.1) 47%, rgba(100, 223, 222, 0.04) 100%);
}
footer p,
footer ul {
  margin: 0;
  padding: 0;
}
footer .footer-box {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  padding: 50px 50px 30px 50px ;
}
footer .footer-box .footer-item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
footer .footer-box .footer-item p {
  font-size: 22px;
  font-family: PingFang-SC-Medium, PingFang-SC;
  font-weight: 500;
  color: #64dfde;
  line-height: 30px;
}
footer .footer-box .footer-item a {
  font-size: 14px;
  font-family: PingFangSC-Light, PingFang SC;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.7);
  line-height: 21px;
  padding: 2px;
}
footer .footer-box li:nth-child(1) {
  flex-direction: row;
}
footer .footer-box li:nth-child(1) img {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}
footer .footer-box li:nth-child(1) p {
  font-size: 28px;
  font-family: PingFang-SC-Medium, PingFang-SC;
  color: #64dfde;
}
footer .footer-box li:nth-child(1) span {
  font-size: 14px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: #64dfde;
  line-height: 20px;
}
footer .footer-box li a:hover {
  text-shadow: 0px 2px 4px #64dfde;
  color: #ffffff;
}
footer .explanationSegmentationImg {
  width: 100%;
  height: 12px;
}
footer .record {
  text-align: center;
  font-size: 14px;
  font-family: PingFangSC-Light, PingFang SC;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.7);
  line-height: 20px;
  text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
  padding-bottom: 10px !important;
}
