nav {
  width: 100%;
  background: -webkit-gradient(linear, right top, left top, from(rgba(100, 223, 222, 0)), color-stop(47%, rgba(100, 223, 222, 0.2)), to(rgba(100, 223, 222, 0)));
  background: linear-gradient(270deg, rgba(100, 223, 222, 0) 0%, rgba(100, 223, 222, 0.2) 47%, rgba(100, 223, 222, 0) 100%);
  border-bottom: 1px solid;
  -webkit-border-image: -webkit-gradient(linear, right top, left top, from(rgba(100, 223, 222, 0.8)), color-stop(rgba(100, 223, 222, 0)), to(#64dfde)) 1 1;
  -webkit-border-image: linear-gradient(270deg, rgba(100, 223, 222, 0.8), rgba(100, 223, 222, 0), #64dfde) 1 1;
          border-image: -webkit-gradient(linear, right top, left top, from(rgba(100, 223, 222, 0.8)), color-stop(rgba(100, 223, 222, 0)), to(#64dfde)) 1 1;
          border-image: linear-gradient(270deg, rgba(100, 223, 222, 0.8), rgba(100, 223, 222, 0), #64dfde) 1 1;
}
nav .upload-box {
  display: inline-block;
  margin-left: 10%;
}
nav .upload-box div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #fff;
  padding: 9.5px 0;
  cursor: pointer;
}
nav .upload-box div .logoImg {
  width: 26px;
  height: 26px;
  margin-right: 15px;
}
nav .logo-info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
nav .logo-info .loginBtn {
  color: #64DFDE;
}
nav .logo-info .switch-language-box {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
nav .logo-info .switch-language-box .headerMap {
  width: 18px;
  height: 18px;
  color: #fff;
}
nav .logo-info .switch-language-box .headerMap:last-of-type {
  fill: #fff;
}
nav .logo-info .switch-language-box .ant-select-arrow {
  color: #fff !important;
}
